.nav {
    display: flex;
    align-items: center;
    width: 1340px;
    max-width: 1340px;
    margin: 0 auto;
    position: relative;
    flex: 0 0 100%;
    display: flex;
    height: 75px;
    z-index: 100;

    @media (max-width: 1500px) {
        width: 100%;
        justify-content: space-between; }

    &__brand {
        flex: 0 0 15%;

        @media (max-width: 915px) {
            flex: 0 0 7%;

            & .logo {

                &:first-child {
                    display: none; }

                &:last-child {
                    display: inline !important; } } }

        @media (max-width: 845px) {
            flex: 0 0 8%; } }

    &__navigation {
        display: flex;
        align-items: center;
        flex: 0 0 70%;
        justify-content: center;

        @media (max-width: 915px) {
            flex: 0 0 75%; }

        @media (max-width: 845px) {
            justify-content: center;
            flex: 0 0 60%;
            padding-left: 16px; }

        @media (max-width: 480px) {
            justify-content: flex-start;
            flex: 0 0 40%; }

        &__item {
            display: inline-flex;
            color: white;
            font-size: 15px;
            white-space: nowrap;
            @include animate();

            &:first-child {
                @media (max-width: 1000px) {
                    display: none; } }

            &:nth-child(2), &:nth-child(3), &:nth-child(4), &:nth-child(5) {
                @media (max-width: 845px) {
                    display: none; } }

            &:last-child {
                @media (max-width: 480px) {
                    display: none; } }

            &:hover {
                background-color: rgba(255, 255, 255, 0.07);
                @include animate(); }

            &--dark {
                color: $dark; }

            &--cta {
                white-space: nowrap;

                &:hover {
                    @include animate();
                    background-color: black; } } }

        &__select {
            display: none;

            @media (max-width: 845px) {
                display: flex; } }

        &__lang {
            display: flex;
            font-size: 13px;

            & ion-icon {
                font-size: 14px; } } }

    &__social {
        display: flex;
        flex: 0 0 15%;
        align-items: center;
        justify-content: flex-end;

        @media (max-width: 915px) {
            flex: 0 0 18%; }

        @media (max-width: 480px) {
            flex: 0 0 38%; }

        & a {
            color: white !important; }

        &--dark a {
            color: $dark !important; } }

    &__wrapper {

        &--fixed {
            width: 100%;
            display: flex;
            justify-content: center;
            background: #ffffffc9;
            height: 75px;
            -webkit-backdrop-filter: blur(30px);
            backdrop-filter: blur(30px);
            z-index: 324234234234234;
            position: fixed;
            top: 0;
            left: 0;
            animation: slide-in .3s;

            @keyframes slide-in {
                0% {
                    top: -75px; }
                100% {
                    top: 0px; } }

            &--remove {
                top: -75px;
                animation: slide-out .3s;

                @keyframes slide-out {
                    0% {
                        top: 0px; }
                    100% {
                        top: -75px; } } } }

        &--dark {

            &.nav__wrapper--fixed {
                background: #1d1d1dc9; } } }

    &--fixed {
        position: fixed;

        @media (max-width: 1300px) {
            padding: 0 50px; }

        @media (max-width: 460px) {
            padding: 0 20px; } } }

// Always display button outside of header (overrides RWD)
main .btn {
    display: inline-block !important; }
